.item {
    width: 100px;
    height: 160px;
    border: 1px solid black;
    margin: 10px 0;
    justify-content: center;
    text-align: center;

    .picture {
        width: 84px;
        height: 84px;
      //  border: 1px solid black;
        margin: 7px;
    }
    .name {
        font-size: 13px;
        font-weight: bold;
    }
    .typeWrapper {
        display: inline-block;
        width: 95px;
        margin-top: 10px;
        .attackTypes {
            border: 1px solid black;
            font-size: 13px;
            border-radius: 3px;
            margin: 6px 3px 0 5px;
        }
    }
}

@media (max-width: 1000px) {
    .item {
        width: 200px;
        height: 240px;
        .picture {
            width: 185px;
            height: 170px;
        }
        .name {
            font-size: 20px;
            font-weight: bold;
        }
        .typeWrapper {
            width: 145px;
            margin-top: 8px;
            .attackTypes {
                font-size: 16px;
            }
        }
    }
}
