.app {
    max-width: 560px;
    padding: 50px 0;
    margin: auto;
    .headerWrapper {
        display: flex;
        justify-content: center;
        max-width: 375px;
        max-height: 50px;
        margin: auto;
        border: 1px solid black;
        .header {
            margin: auto;
            font-size: 28px;
            font-weight: bold;
        }
    }

    .content {
        display: flex;
        .pokemonList {
            max-width: 340px;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            margin-top: 10px;
        }
        .infoWrapper {
            .typeFilter {
                margin-left: 55px;
                position: absolute;
            }
            .pokemonInfo {
                margin: 202px 0 0 27px;
            }
        }
    }
}

@media (max-width: 1000px) {
    .app {
        max-width: 720px;
        .content {
            .pokemonList {
                max-width: 420px;
            }
            .infoWrapper {
                margin-left: 20px;
                margin-top: 80px;
            }
        }
    }
}
